<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            num: 0,
            title: '产品详情',
            arr: [],
            list: [{
                t: '产品特点'
            }, {
                t: '技术参数'
            }, {
                t: '外观尺寸'
            }, {
                t: '订货须知'
            }, {
                t: '注意事项'
            }]
        }
    },
    mounted() {
        var arr = window.sessionStorage.getItem('product_details')
        this.arr = JSON.parse(arr)
        console.log(this.arr)
    },
    methods: {
    	edit(){
    		this.$router.push('/home/product_edit')
    	},
        fn(index) {
            this.num = index
        },
        rowClass({ row, rowIndex }) {
            return "text-align:center;background:#f5f5f5;color:#666"
        },
        cellStyle({ row, column, rowIndex, columnIndex }) {
            return "text-align:center;"
        },
        del() {
            this.$confirm('是否删除?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                if (action === 'confirm') {
                    this.$axios.post('/tomlive/productInfo/deleteProductInfoByPrimaryKey', this.$qs.stringify({
                        id: this.arr.id
                    })).then((res) => {
                        console.log(res)
                        this.$router.push('/home/product_manage')
                    })
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            })
        }
    }
}
</script>
